"use client"

import { useEffect, useState } from "react"
import {
  Plus,
  Edit2,
  Trash2,
  X,
  FolderTree,
  Search,
  LayoutGrid,
  List,
  Info,
} from "lucide-react"

const API_BASE_URL =
  process.env.NEXT_PUBLIC_API_BASE_URL ?? "https://localhost:7105"

type ViewMode = "list" | "grid"
const VIEW_MODE_KEY = "adminCategoriasViewMode"

// ===== Tipos da API (CategoryResponseDto) =====
interface CategoryApi {
  id: string
  name: string
  description: string | null
  isActive: boolean
  createdAt: string
}

// Modelo usado na tela
interface Category {
  id: string
  nome: string
  slug: string
  descricao: string
  ativo: boolean
  produtosCount: number
  createdAt: string
}

// Gera slug a partir do nome (apenas visual)
const generateSlug = (nome: string) => {
  return nome
    .toLowerCase()
    .normalize("NFD")
    .replace(/[\u0300-\u036f]/g, "")
    .replace(/[^a-z0-9]+/g, "-")
    .replace(/(^-|-$)/g, "")
}

export default function AdminCategorias() {
  const [categories, setCategories] = useState<Category[]>([])
  const [showModal, setShowModal] = useState(false)
  const [editingCategory, setEditingCategory] = useState<Category | null>(null)
  const [formData, setFormData] = useState({
    nome: "",
    descricao: "",
    ativo: true,
  })

  const [searchTerm, setSearchTerm] = useState("")
  const [loading, setLoading] = useState(false)
  const [saving, setSaving] = useState(false)

  const [viewMode, setViewMode] = useState<ViewMode>(() => {
    if (typeof window === "undefined") return "list"
    const saved = window.localStorage.getItem(VIEW_MODE_KEY) as ViewMode | null
    return saved === "list" || saved === "grid" ? saved : "list"
  })

  const changeViewMode = (mode: ViewMode) => {
    setViewMode(mode)
    if (typeof window !== "undefined") {
      window.localStorage.setItem(VIEW_MODE_KEY, mode)
    }
  }

  const mapApiToCategory = (c: CategoryApi): Category => ({
    id: c.id,
    nome: c.name,
    slug: generateSlug(c.name),
    descricao: c.description ?? "",
    ativo: c.isActive,
    produtosCount: 0, // por enquanto, até ligar com produtos
    createdAt: c.createdAt,
  })

  const fetchCategories = async () => {
    try {
      setLoading(true)
      const res = await fetch(`${API_BASE_URL}/api/admin/categories`)

      if (!res.ok) {
        console.error("Erro ao buscar categorias:", await res.text())
        return
      }

      const data: CategoryApi[] = await res.json()
      setCategories(data.map(mapApiToCategory))
    } catch (err) {
      console.error("Erro ao buscar categorias:", err)
    } finally {
      setLoading(false)
    }
  }

  useEffect(() => {
    void fetchCategories()
  }, [])

  // ===== Filtro =====
  const filteredCategories = categories.filter((c) =>
    c.nome.toLowerCase().includes(searchTerm.toLowerCase()),
  )

  // ===== Ordenação: ativos primeiro, inativos no final =====
  const sortedCategories = [...filteredCategories].sort((a, b) => {
    // Ativos vêm primeiro
    if (a.ativo !== b.ativo) {
      return a.ativo ? -1 : 1
    }

    // Dentro do mesmo status → mais novos primeiro
    const dateA = new Date(a.createdAt).getTime()
    const dateB = new Date(b.createdAt).getTime()
    return dateB - dateA
  })

  const openCreateModal = () => {
    setEditingCategory(null)
    setFormData({ nome: "", descricao: "", ativo: true })
    setShowModal(true)
  }

  const openEditModal = (category: Category) => {
    setEditingCategory(category)
    setFormData({
      nome: category.nome,
      descricao: category.descricao,
      ativo: category.ativo,
    })
    setShowModal(true)
  }

  const handleSave = async () => {
    if (!formData.nome.trim()) {
      alert("Informe o nome da categoria.")
      return
    }

    setSaving(true)

    const payloadCreate = {
      // CreateCategoryDto
      name: formData.nome,
      description: formData.descricao || null,
    }

    const payloadUpdate = {
      // UpdateCategoryDto
      name: formData.nome,
      description: formData.descricao || null,
      isActive: formData.ativo,
    }

    try {
      if (editingCategory) {
        // PUT /api/admin/categories/{id}
        const res = await fetch(
          `${API_BASE_URL}/api/admin/categories/${editingCategory.id}`,
          {
            method: "PUT",
            headers: {
              "Content-Type": "application/json",
            },
            body: JSON.stringify(payloadUpdate),
          },
        )

        if (!res.ok) {
          console.error("Erro ao atualizar categoria:", await res.text())
          alert("Não foi possível atualizar a categoria.")
          return
        }
      } else {
        // POST /api/admin/categories
        const res = await fetch(`${API_BASE_URL}/api/admin/categories`, {
          method: "POST",
          headers: {
            "Content-Type": "application/json",
          },
          body: JSON.stringify(payloadCreate),
        })

        if (!res.ok) {
          console.error("Erro ao criar categoria:", await res.text())
          alert("Não foi possível criar a categoria.")
          return
        }
      }

      await fetchCategories()
      setShowModal(false)
    } catch (err) {
      console.error("Erro ao salvar categoria:", err)
      alert("Ocorreu um erro ao salvar a categoria.")
    } finally {
      setSaving(false)
    }
  }

  const handleDelete = async (id: string) => {
    if (!confirm("Tem certeza que deseja excluir (desativar) esta categoria?"))
      return

    try {
      const res = await fetch(`${API_BASE_URL}/api/admin/categories/${id}`, {
        method: "DELETE",
      })

      if (!res.ok && res.status !== 204) {
        console.error("Erro ao excluir categoria:", await res.text())
        alert("Não foi possível excluir a categoria.")
        return
      }

      await fetchCategories()
    } catch (err) {
      console.error("Erro ao excluir categoria:", err)
      alert("Ocorreu um erro ao excluir a categoria.")
    }
  }

  const toggleStatus = async (id: string) => {
    const category = categories.find((c) => c.id === id)
    if (!category) return

    const payloadUpdate = {
      name: category.nome,
      description: category.descricao || null,
      isActive: !category.ativo,
    }

    try {
      const res = await fetch(`${API_BASE_URL}/api/admin/categories/${id}`, {
        method: "PUT",
        headers: {
          "Content-Type": "application/json",
        },
        body: JSON.stringify(payloadUpdate),
      })

      if (!res.ok) {
        console.error("Erro ao alterar status:", await res.text())
        alert("Não foi possível alterar o status da categoria.")
        return
      }

      setCategories((prev) =>
        prev.map((c) =>
          c.id === id ? { ...c, ativo: !c.ativo } : c,
        ),
      )
    } catch (err) {
      console.error("Erro ao alterar status:", err)
      alert("Ocorreu um erro ao alterar o status da categoria.")
    }
  }

  const slugPreview = generateSlug(formData.nome || "categoria")

  return (
    <div className="p-8">
      {/* Header */}
      <div className="flex items-center justify-between mb-8">
        <div>
          <h1 className="text-3xl font-light text-foreground">Categorias</h1>
          <p className="text-foreground/60">
            Organize seus produtos em categorias
          </p>
        </div>
        <button
          onClick={openCreateModal}
          className="flex items-center gap-2 bg-primary text-primary-foreground px-4 py-2 rounded-lg font-medium hover:bg-primary/90 transition-colors"
        >
          <Plus size={18} />
          Nova Categoria
        </button>
      </div>

      {/* Alerta de Instruções de Exclusão */}
      <div className="mb-6 bg-blue-50 border border-blue-200 rounded-lg p-4 flex gap-3">
        <Info className="text-blue-600 flex-shrink-0 mt-0.5" size={20} />
        <div className="text-sm text-blue-900">
          <p className="font-medium mb-1">Como funciona a exclusão:</p>
          <ul className="list-disc list-inside space-y-1 text-blue-800">
            <li><strong>1º clique na lixeira:</strong> A categoria fica inativa.</li>
            <li><strong>2º clique na lixeira:</strong> Se não houver vínculos com produtos, a categoria é removida permanentemente. Se houver vínculos, permanece inativa.</li>
          </ul>
        </div>
      </div>

      {/* Filtros & Toggle de visualização */}
      <div className="flex flex-col sm:flex-row gap-4 mb-6 justify-between">
        <div className="relative flex-1 max-w-md">
          <Search
            size={18}
            className="absolute left-3 top-1/2 -translate-y-1/2 text-foreground/50"
          />
          <input
            type="text"
            placeholder="Buscar por nome..."
            value={searchTerm}
            onChange={(e) => setSearchTerm(e.target.value)}
            className="w-full pl-10 pr-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
          />
        </div>
        <div className="flex items-center bg-muted rounded-lg p-1 border border-border">
          <button
            onClick={() => changeViewMode("list")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "list"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <List size={18} />
          </button>
          <button
            onClick={() => changeViewMode("grid")}
            className={`p-2 rounded-md transition-all ${
              viewMode === "grid"
                ? "bg-background shadow-sm text-foreground"
                : "text-foreground/60 hover:text-foreground"
            }`}
          >
            <LayoutGrid size={18} />
          </button>
        </div>
      </div>

      {/* Conteúdo */}
      {loading ? (
        <div className="py-12 text-center text-foreground/60">
          Carregando categorias...
        </div>
      ) : viewMode === "grid" ? (
        <div className="grid grid-cols-1 md:grid-cols-2 lg:grid-cols-3 gap-4">
          {sortedCategories.map((category) => {
            const isInactive = !category.ativo

            return (
              <div
                key={category.id}
                className={`bg-card border rounded-xl p-6 transition-all ${
                  isInactive
                    ? "border-border/50 opacity-70"
                    : "border-border"
                }`}
              >
                <div className="flex items-start justify-between mb-4">
                  <div
                    className={`p-3 rounded-lg bg-muted ${
                      isInactive ? "opacity-70" : ""
                    }`}
                  >
                    <FolderTree
                      size={24}
                      className={
                        isInactive ? "text-foreground/40" : "text-accent"
                      }
                    />
                  </div>
                  <div className="flex items-center gap-1">
                    <button
                      onClick={() => openEditModal(category)}
                      className={`p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors ${
                        isInactive ? "opacity-60" : ""
                      }`}
                    >
                      <Edit2 size={16} />
                    </button>
                    <button
                      onClick={() => handleDelete(category.id)}
                      className={`p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors ${
                        isInactive ? "opacity-60" : ""
                      }`}
                    >
                      <Trash2 size={16} />
                    </button>
                  </div>
                </div>

                <h3
                  className={`font-medium mb-1 ${
                    isInactive ? "text-foreground/70" : "text-foreground"
                  }`}
                >
                  {category.nome}
                </h3>
                <p
                  className={`text-sm mb-2 ${
                    isInactive ? "text-foreground/50" : "text-foreground/60"
                  }`}
                >
                  /{category.slug}
                </p>
                {category.descricao && (
                  <p
                    className={`text-sm mb-4 line-clamp-2 ${
                      isInactive
                        ? "text-foreground/60"
                        : "text-foreground/70"
                    }`}
                  >
                    {category.descricao}
                  </p>
                )}

                <div className="flex items-center justify-between">
                  <span
                    className={`text-sm ${
                      isInactive
                        ? "text-foreground/60"
                        : "text-foreground/60"
                    }`}
                  >
                    {category.produtosCount} produtos
                  </span>
                  <button
                    onClick={() => toggleStatus(category.id)}
                    className={`px-3 py-1 rounded-full text-xs font-medium transition-colors ${
                      category.ativo
                        ? "bg-green-100 text-green-700 hover:bg-green-200"
                        : "bg-red-100 text-red-700 hover:bg-red-200"
                    }`}
                  >
                    {category.ativo ? "Ativo" : "Inativo"}
                  </button>
                </div>
              </div>
            )
          })}
        </div>
      ) : (
        <div className="bg-card border border-border rounded-xl overflow-hidden">
          <div className="overflow-x-auto">
            <table className="w-full">
              <thead className="bg-muted border-b border-border">
                <tr>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Categoria
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Slug
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Produtos
                  </th>
                  <th className="px-6 py-4 text-left text-sm font-medium text-foreground">
                    Status
                  </th>
                  <th className="px-6 py-4 text-center text-sm font-medium text-foreground">
                    Ações
                  </th>
                </tr>
              </thead>
              <tbody className="divide-y divide-border">
                {sortedCategories.map((category) => {
                  const isInactive = !category.ativo

                  return (
                    <tr
                      key={category.id}
                      className={`transition-colors ${
                        isInactive
                          ? "bg-muted/50 opacity-70"
                          : "hover:bg-muted/50"
                      }`}
                    >
                      <td className="px-6 py-4">
                        <div className="flex items-center gap-3">
                          <div
                            className={`p-2 rounded-lg bg-muted ${
                              isInactive ? "opacity-70" : ""
                            }`}
                          >
                            <FolderTree
                              size={16}
                              className={
                                isInactive
                                  ? "text-foreground/40"
                                  : "text-accent"
                              }
                            />
                          </div>
                          <span
                            className={`font-medium ${
                              isInactive
                                ? "text-foreground/70"
                                : "text-foreground"
                            }`}
                          >
                            {category.nome}
                          </span>
                        </div>
                      </td>
                      <td
                        className={`px-6 py-4 text-sm ${
                          isInactive
                            ? "text-foreground/60"
                            : "text-foreground/70"
                        }`}
                      >
                        /{category.slug}
                      </td>
                      <td
                        className={`px-6 py-4 text-sm ${
                          isInactive
                            ? "text-foreground/60"
                            : "text-foreground/70"
                        }`}
                      >
                        {category.produtosCount}
                      </td>
                      <td className="px-6 py-4">
                        <button
                          onClick={() => toggleStatus(category.id)}
                          className={`px-2 py-1 rounded-full text-xs font-medium transition-colors ${
                            category.ativo
                              ? "bg-green-100 text-green-700 hover:bg-green-200"
                              : "bg-red-100 text-red-700 hover:bg-red-200"
                          }`}
                        >
                          {category.ativo ? "Ativo" : "Inativo"}
                        </button>
                      </td>
                      <td className="px-6 py-4">
                        <div className="flex items-center justify-center gap-2">
                          <button
                            onClick={() => openEditModal(category)}
                            className={`p-2 text-foreground/60 hover:text-primary hover:bg-muted rounded-lg transition-colors ${
                              isInactive ? "opacity-60" : ""
                            }`}
                          >
                            <Edit2 size={16} />
                          </button>
                          <button
                            onClick={() => handleDelete(category.id)}
                            className={`p-2 text-foreground/60 hover:text-red-600 hover:bg-red-50 rounded-lg transition-colors ${
                              isInactive ? "opacity-60" : ""
                            }`}
                          >
                            <Trash2 size={16} />
                          </button>
                        </div>
                      </td>
                    </tr>
                  )
                })}

                {sortedCategories.length === 0 && !loading && (
                  <tr>
                    <td
                      colSpan={5}
                      className="px-6 py-8 text-center text-foreground/60"
                    >
                      Nenhuma categoria encontrada.
                    </td>
                  </tr>
                )}
              </tbody>
            </table>
          </div>
        </div>
      )}

      {/* Modal */}
      {showModal && (
        <div className="fixed inset-0 bg-black/50 flex items-center justify-center z-50 p-4">
          <div className="bg-card border border-border rounded-xl w-full max-w-md">
            <div className="flex items-center justify-between p-6 border-b border-border">
              <h2 className="text-xl font-medium text-foreground">
                {editingCategory ? "Editar Categoria" : "Nova Categoria"}
              </h2>
              <button
                onClick={() => setShowModal(false)}
                className="text-foreground/60 hover:text-foreground"
              >
                <X size={20} />
              </button>
            </div>

            <div className="p-6 space-y-4">
              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Nome da Categoria
                </label>
                <input
                  type="text"
                  value={formData.nome}
                  onChange={(e) =>
                    setFormData({ ...formData, nome: e.target.value })
                  }
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent"
                  placeholder="Ex: Papelaria Personalizada"
                />
                <p className="text-xs text-foreground/60 mt-1">
                  Slug gerado:{" "}
                  <span className="font-mono">/{slugPreview}</span>
                </p>
              </div>

              <div>
                <label className="block text-sm font-medium text-foreground mb-2">
                  Descrição (opcional)
                </label>
                <textarea
                  value={formData.descricao}
                  onChange={(e) =>
                    setFormData({ ...formData, descricao: e.target.value })
                  }
                  className="w-full px-4 py-2 border border-border rounded-lg bg-background focus:outline-none focus:ring-2 focus:ring-accent min-h-[80px]"
                  placeholder="Descrição breve da categoria..."
                />
              </div>

              <div className="flex items-center gap-3">
                <input
                  type="checkbox"
                  id="ativo"
                  checked={formData.ativo}
                  onChange={(e) =>
                    setFormData({ ...formData, ativo: e.target.checked })
                  }
                  className="accent-accent"
                />
                <label htmlFor="ativo" className="text-sm text-foreground">
                  Categoria ativa
                </label>
              </div>
            </div>

            <div className="flex items-center justify-end gap-3 p-6 border-t border-border">
              <button
                onClick={() => setShowModal(false)}
                className="px-4 py-2 border border-border rounded-lg hover:bg-muted transition-colors"
              >
                Cancelar
              </button>
              <button
                onClick={handleSave}
                disabled={saving}
                className="px-4 py-2 bg-primary text-primary-foreground rounded-lg hover:bg-primary/90 transition-colors disabled:opacity-70 disabled:cursor-not-allowed"
              >
                {saving
                  ? "Salvando..."
                  : editingCategory
                  ? "Salvar"
                  : "Criar"}
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  )
}